
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{ 
  font-size: 1.6rem;
}
h1{
  font-size: 6rem;
}
h3{
  font-size: 4rem;
}
.app{
  width: 100%;
  height: 100% !important;
  position: relative;
  background-color: rgba(0,0,0,0.3);
  color: #000;
}

.app:before{
  content:'';
  background: url(/weather-forecast/static/media/bg.d54ed835.png) no-repeat center center/cover;
  background-color: #1976D2;
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  left: 0;
  z-index: -1;
}
.app .search{
  text-align: center;
  padding: 1rem;
}
.pushdown{
  margin: 10px auto 3px auto;
  width: 100%;
}
.clearboth{
  margin-bottom: 1.5rem;
}
.app input{
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255,0.8);
  background: rgba(255,255,255,0.1);
  color: #f8f8f8;
}

::-webkit-input-placeholder{
  color: #f8f8f8;
}

::placeholder{
  color: #f8f8f8;
}
.container{
  max-width: 350px;
  height: 100vh !important;
  margin: auto;
  padding: 0 1rem;
  top: 5%;
  bottom: 5%;
  display: grid;
}

.app .top{
  width:  100%;
  margin: 1rem auto
}

.container .top{
  color: white;
}

/* .app .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
} */

/* .app .description{
  position: relative;
  right: -50%;
  transform-origin: 0 0;
  transform: rotate(0deg);
} */

.app .bottom{
  display: flex;
  justify-content:  space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
}

.bold{
  font-size: 700;
}
.small{
  font-size: 0.9rem;
}

.thin{
  font-size: 0.9rem;
}
.curent-weather-caption{
    display: grid;
}
.widget-top, .widget-bottom{
  display: flex;
  justify-content:  space-evenly;
  text-align: center;
  color: white;
  width: 350px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0.2rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
}

.widget-top .location{
    text-align: start;
}

.widget-top .description{
    text-align: start; 
}
.flexit{
display: flex;
justify-content: flex-start;
align-items: center;
padding: 7px;
}
.widget-top .description h4{
    font-size: 2.2rem;
}
.widget-top .description p{
    font-size: 1rem;
    font-family: serif;
    padding-right: 3px; 
}
img.weather-icon{
    width: 50px;
}
.title{
    font-size: 1.25rem;
    font-weight: 700;
    margin: auto;
}

.daily-items{
background-color: #f5f5f5;
height: 40px;
margin: 0rem auto;
display: flex;
align-items: center;
cursor: pointer;
font-size:14px;
padding: 5px 8px;
justify-content:space-evenly;
border-radius: 20px 0px;
width: 350px;
}

.small-icon{
    width: 40px;
}

.day{
color: #212121;
flex: 1 1;
font-weight: 600;
margin-left: 15px;
}
.daily-items .description {
    flex: 1 1;
    margin-right: 15px;
    text-align: right;
}

.min-max{
    color: #757575;
}

.details-grid{
grid-row-gap: 0;
grid-column-gap: 15px;
grid-row-gap: 0;
row-gap: 0;
grid-column-gap: 15px;
-webkit-column-gap: 15px;
        column-gap: 15px;
display: grid;
flex: 1 1;
grid-template-columns: auto auto;
padding: 5px 15px;
margin-top: 0px;
background: white;
-webkit-columns: black;
        columns: black;
width: 350px;
transition: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.items-grid{
display: flex;
height: 30px;
justify-content: space-between;
align-items: center;
}

.items-grid label:first-child{
    color: #757575;
}

.items-grid label:last-child{
    color: #212121;
}
